.page-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: transparent;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  z-index: 100;
}
.page-header.collapse {
    background-color: #fff;
    -webkit-box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
            box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
    opacity: 0.98;
}
.page-header.open {
    background-color: #fff;
}
.nav-container {
  margin: 0 auto;
  width: 90%;
  color: #1d1d1f;
}
.nav-container .nav-left {
    float: left;
}
.nav-container .nav-left .logo {
      position: relative;
      height: 80px;
      line-height: 80px;
}
.nav-container .nav-left .logo img {
        position: absolute;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        width: 120px;
}
.nav-container .nav-left .logo span {
        margin-left: 40px;
        color: #3473e7;
        font-weight: bold;
        font-size: 16px;
}
.nav-container .nav-left .nav-icon {
      display: none;
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -10px;
      font-size: 20px;
}
.nav-container .nav-right {
    float: right;
}
.nav-container .nav-right .navbar {
      display: inline-block;
      margin: 0;
      padding: 0;
      height: 80px;
      font-size: 14px;
      line-height: 80px;
      list-style-type: none;
}
.nav-container .nav-right .navbar a {
        display: inline-block;
        color: #1d1d1f;
}
.nav-container .nav-right .navbar a::after {
          content: "";
          display: none;
          position: absolute;
          bottom: 0;
          left: 0;
          height: 2px;
          width: 100%;
          background-color: #6190e8;
}
.nav-container .nav-right .navbar a.router-link-active {
          color: #6190E8;
}
.nav-container .nav-right .navbar a.router-link-active::after {
            display: inline-block;
}
.nav-container .nav-right .navbar li {
        position: relative;
        display: inline-block;
        margin: 0 24px;
        cursor: pointer;
        -webkit-transition: color 0.3s;
        transition: color 0.3s;
}
.nav-container .nav-right .navbar li:hover {
          color: #6190E8;
}
.nav-container .nav-right .navbar li:hover a {
            color: #6190E8;
}
.nav-container .nav-right .navbar li.disabled {
          color: #c9c9c9;
          cursor: not-allowed;
}
.nav-container .nav-right .btn-language {
      display: inline-block;
      margin: 0 24px;
      padding: 2px 12px;
      border: 1px solid #1d1d1f;
      border-radius: 2px;
      cursor: pointer;
      -webkit-transition: color 0.3s, border 0.3s;
      transition: color 0.3s, border 0.3s;
}
.nav-container .nav-right .btn-language:hover {
        color: #6190E8;
        border-color: #6190E8;
}
@media screen and (max-width: 767px) {
.page-header {
    height: 60px;
}
.page-header.open .nav-right {
      height: 220px;
}
.nav-container {
    width: initial;
}
.nav-container .nav-left {
      position: relative;
      margin: 0 20px;
      float: none;
      text-align: center;
}
.nav-container .nav-left .logo {
        display: inline-block;
        height: 60px;
        line-height: 60px;
}
.nav-container .nav-left .nav-icon {
        display: inline-block;
}
.nav-container .nav-right {
      float: none;
      height: 0;
      text-align: center;
      background-color: #fff;
      -webkit-box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
              box-shadow: 0 10px 60px 0 rgba(29, 29, 31, 0.07);
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
      overflow: hidden;
}
.nav-container .nav-right .navbar {
        display: block;
        height: initial;
        line-height: 50px;
}
.nav-container .nav-right .navbar li {
          display: block;
}
.nav-container .nav-right .navbar a {
          width: 100%;
}
.nav-container .nav-right .navbar a.router-link-active::after {
            display: none;
}
.nav-container .nav-right .btn-language {
        margin: 16px;
}
}
